// App.js
import React, { useState, useEffect } from 'react';
import {
  View,
  Text,
  TextInput,
  Button,
  StyleSheet,
  ActivityIndicator,
  FlatList,
  TouchableOpacity
} from 'react-native';
import { SafeAreaProvider, SafeAreaView } from 'react-native-safe-area-context';
import { Picker } from '@react-native-picker/picker';
import * as SQLite from 'expo-sqlite';
import {
  initDatabase,
  insertInitialDataIfNeeded,
  getUserState,
  updateUserState,
  getDailyChallenge,
  incrementDailyChallenge,
  insertSessionRecord
} from './db';
import { v4 as uuidv4 } from 'uuid';

export default function App() {
  const [db, setDb] = useState(null);
  const [mode, setMode] = useState('menu');
  const [filters, setFilters] = useState({ maxGrade: 6, maxDiff: 7 });
  const [question, setQuestion] = useState(null);
  const [answer, setAnswer] = useState('');
  const [feedback, setFeedback] = useState('');
  const [history, setHistory] = useState([]);
  const [dataList, setDataList] = useState([]);
  const [loading, setLoading] = useState(false);
  const [qCount, setQCount] = useState(0);
  const [score, setScore] = useState(0);

  // Gamification state
  const [xpTotal, setXpTotal] = useState(0);
  const [currentLevel, setCurrentLevel] = useState(1);
  const [xpToNext, setXpToNext] = useState(100);
  const [daily, setDaily] = useState({ goal: 0, progress: 0 });
  const [streak, setStreak] = useState(0);

  // Session results state
  const [sessionXP, setSessionXP] = useState(0);

  useEffect(() => {
    (async () => {
      await initDatabase();
      await insertInitialDataIfNeeded();
      const database = await SQLite.openDatabaseAsync('kanji.db');
      setDb(database);

      const us = await getUserState();
      setXpTotal(us.xp_total);
      setCurrentLevel(us.current_level);
      setXpToNext(us.xp_to_next_level);
      setStreak(us.streak_count);

      const dc = await getDailyChallenge();
      setDaily({ goal: dc.goal, progress: dc.progress });
    })();
  }, []);

  const getDatabase = async () => {
    if (!db) {
      const database = await SQLite.openDatabaseAsync('kanji.db');
      setDb(database);
      return database;
    }
    return db;
  };

  const startQuiz = async () => {
    const database = await getDatabase();
    setQCount(0);
    setScore(0);
    setHistory([]);
    setSessionXP(0); // セッション開始時にXPリセット
    setMode('quiz');
    await loadRandomQuestion(database);
  };

  const loadRandomQuestion = async (databaseParam) => {
    const database = databaseParam || (await getDatabase());
    setFeedback('');
    setAnswer('');
    setLoading(true);
    try {
      const row = await database.getFirstAsync(
        `SELECT e.id, e.sentence, e.target, e.reading, e.difficulty, k.grade
         FROM examples e
         JOIN kanji k ON e.kanji_id = k.id
         WHERE k.grade <= ? AND e.difficulty <= ?
         ORDER BY RANDOM() LIMIT 1;`,
        filters.maxGrade,
        filters.maxDiff
      );
      setQuestion(row);
    } catch (error) {
      console.error('loadRandomQuestion error:', error);
    } finally {
      setLoading(false);
    }
  };

  const checkAnswer = async () => {
    if (!question) return;
    const correct = answer.trim() === question.reading;
    setFeedback(correct ? '✅ 正解！' : `❌ 不正解。正解は「${question.reading}」`);
    setHistory(h => [...h, { question, yourAnswer: answer.trim(), correct }]);
    setScore(s => s + (correct ? 1 : 0));

    let dailyBonusXP = 0;
    
    // デイリーチャレンジの更新（正解時のみ）
    if (correct) {
      const dc = await incrementDailyChallenge();
      setDaily({ goal: dc.goal, progress: dc.progress });
      dailyBonusXP = dc.bonus_xp || 0;
      
      if (dailyBonusXP > 0) {
        setFeedback(prev => prev + ` 🎉 デイリーチャレンジ完了！ボーナス+${dailyBonusXP}XP`);
      }
    }

    // ユーザ状態の更新
    console.log('Before updateUserState:', { correct, dailyBonusXP });
    const us = await updateUserState({ 
      isCorrect: correct, 
      resetStreak: !correct,
      dailyBonusXP: dailyBonusXP
    });
    
    console.log('After updateUserState:', us);
    
    setXpTotal(us.xp_total);
    setCurrentLevel(us.current_level);
    setXpToNext(us.xp_to_next_level);
    setStreak(us.streak_count);
    
    // レベルアップ通知
    if (us.level_up) {
      setFeedback(prev => prev + ` 🎊 レベルアップ！ Level ${us.current_level}!`);
    }
    
    // XP獲得通知
    if (us.awarded_xp > 0) {
      setSessionXP(prev => prev + us.awarded_xp); // セッション合計XP更新
      setFeedback(prev => prev + ` (+${us.awarded_xp}XP)`);
    }
  };

  const handleNext = async () => {
    console.log('handleNext called, qCount:', qCount, 'score:', score);
    
    // If it's the last question (10th), go to result
    if (qCount >= 9) {
      console.log('Going to result screen');
      setMode('result');
      
      // Save session record asynchronously without blocking UI
      const avgMs = 0; // TODO: measure actual response time
      const stars = calculateStars(score, 10, avgMs / 1000);
      
      insertSessionRecord({
        sessionId: uuidv4(),
        date: new Date().toISOString().slice(0, 10),
        correctCount: score,
        totalCount: 10,
        avgMs,
        stars,
      }).catch(error => {
        console.error('Error saving session record:', error);
      });
      
      return; // Add return to prevent further execution
    }
    
    // Otherwise, proceed to next question
    console.log('Going to next question');
    const next = qCount + 1;
    setQCount(next);
    await loadRandomQuestion();
  };

  const calculateStars = (correct, total, avgSec) => {
    const rate = correct / total;
    if (rate >= 0.9 && avgSec <= 5) return 3;
    if (rate >= 0.8 || avgSec <= 10) return 2;
    return 1;
  };

  const loadDataList = async () => {
    setLoading(true);
    try {
      const database = await getDatabase();
      const rows = await database.getAllAsync(
        `SELECT e.sentence, e.target, e.reading, e.difficulty, k.grade
         FROM examples e
         JOIN kanji k ON e.kanji_id = k.id
         ORDER BY k.grade, e.difficulty;`
      );
      setDataList(rows);
    } catch (error) {
      console.error('loadDataList error:', error);
    } finally {
      setLoading(false);
    }
  };

  const goMode = async (m) => {
    setMode(m);
    setFeedback('');
    setAnswer('');
    
    if (m === 'data') {
      loadDataList();
    } else if (m === 'menu') {
      // メニューに戻る時にユーザー状態を再取得
      try {
        const us = await getUserState();
        setXpTotal(us.xp_total);
        setCurrentLevel(us.current_level);
        setXpToNext(us.xp_to_next_level);
        setStreak(us.streak_count);
        
        const dc = await getDailyChallenge();
        setDaily({ goal: dc.goal, progress: dc.progress });
        
        console.log('User state refreshed:', us);
      } catch (error) {
        console.error('Error refreshing user state:', error);
      }
    }
  };

  return (
    <SafeAreaProvider>
      <SafeAreaView style={styles.container}>
        {/* Menu */}
        {mode === 'menu' && (
          <View style={styles.menuContainer}>
            <View style={styles.gamificationHeader}>
              <Text style={styles.xpText}>Level {currentLevel}</Text>
              <Text style={styles.xpBar}>XP: {xpTotal} / {xpTotal + xpToNext}</Text>
              <Text style={styles.dailyText}>今日のチャレンジ {daily.progress}/{daily.goal}</Text>
              <Text style={styles.streakText}>🔥 連続正解: {streak}問</Text>
            </View>
            <TouchableOpacity style={styles.menuCard} onPress={startQuiz}>
              <Text style={styles.menuIcon}>🎯</Text>
              <Text style={styles.menuText}>クイズ開始</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.menuCard} onPress={() => goMode('filters')}>
              <Text style={styles.menuIcon}>🔧</Text>
              <Text style={styles.menuText}>フィルター設定</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.menuCard} onPress={() => goMode('data')}>
              <Text style={styles.menuIcon}>📄</Text>
              <Text style={styles.menuText}>データ一覧</Text>
            </TouchableOpacity>
            <TouchableOpacity style={styles.menuCard} onPress={() => goMode('history')}>
              <Text style={styles.menuIcon}>📜</Text>
              <Text style={styles.menuText}>解答履歴</Text>
            </TouchableOpacity>
          </View>
        )}
        {/* Filters */}
        {mode === 'filters' && (
          <View>
            <Text style={styles.subtitle}>🔧 フィルター設定</Text>
            <Text>最大学年:</Text>
            <Picker selectedValue={filters.maxGrade} onValueChange={v => setFilters(f => ({ ...f, maxGrade: v }))}>
              {[1,2,3,4,5,6].map(g => <Picker.Item key={g} label={`${g}年`} value={g} />)}
            </Picker>
            <Text>最大難易度:</Text>
            <Picker selectedValue={filters.maxDiff} onValueChange={v => setFilters(f => ({ ...f, maxDiff: v }))}>
              {[1,2,3,4,5,6,7].map(d => <Picker.Item key={d} label={`${d}`} value={d} />)}
            </Picker>
            <Button title="トップに戻る" onPress={() => goMode('menu')} />
          </View>
        )}
        {/* Quiz */}
        {mode === 'quiz' && (
          loading ? <ActivityIndicator size="large" /> : (
            question && (
              <View>
                <Text style={styles.sentence}>{`${qCount+1}. ${question.sentence}`}</Text>
                <Text style={styles.prompt}>「{question.target}」の読みを入力</Text>
                <TextInput style={styles.input} value={answer} onChangeText={setAnswer} placeholder="ひらがな" />
                <Button title="答える" onPress={checkAnswer} />
                {feedback !== '' && (
                  <View style={styles.feedbackContainer}>
                    <Text style={styles.feedback}>{feedback}</Text>
                    <Text style={{fontSize: 12, color: 'gray'}}>
                      Debug: qCount={qCount}, showing button: {qCount >= 9 ? "結果へ" : "次へ"}
                    </Text>
                    <Button 
                      title={qCount >= 9 ? "結果へ" : "次へ"} 
                      onPress={handleNext} 
                    />
                  </View>
                )}
                <Text style={styles.diff}>学年:{question.grade} 難易度:{question.difficulty}</Text>
                <Button title="トップに戻る" onPress={() => goMode('menu')} />
              </View>
            )
          )
        )}
        {/* Result */}
        {mode === 'result' && (
          <View style={styles.resultContainer}>
            <Text style={styles.resultText}>10問中 {score}問 正解！</Text>
            <Text style={styles.xpEarnedText}>獲得XP: {sessionXP}</Text>
            <Text style={styles.levelInfoText}>Level {currentLevel} (Total XP: {xpTotal})</Text>
            <Button title="トップに戻る" onPress={() => goMode('menu')} />
          </View>
        )}
        {/* Data List */}
        {mode === 'data' && (
          loading ? <ActivityIndicator size="large" /> : (
            <FlatList
              data={dataList}
              keyExtractor={(_,i) => String(i)}
              renderItem={({item}) => (
                <View style={styles.item}>
                  <Text>{item.sentence}</Text>
                  <Text>→ {item.target}／{item.reading}</Text>
                  <Text>学年:{item.grade} diff:{item.difficulty}</Text>
                </View>
              )}
            />
          )
        )}
        {/* History */}
        {mode === 'history' && (
          history.length === 0 ? <Text>まだ解答履歴がありません。</Text> : (
            <FlatList
              data={history}
              keyExtractor={(_,i) => String(i)}
              renderItem={({item,index}) => (
                <View style={styles.item}>
                  <Text>{index+1}. {item.question.sentence}</Text>
                  <Text>あなた: {item.yourAnswer} → {item.correct ? '○' : '×'}</Text>
                  <Text>学年:{item.question.grade} diff:{item.question.difficulty}</Text>
                </View>
              )}
            />
          )
        )}
        {/* Back */}
        {mode !== 'menu' && mode !== 'quiz' && mode !== 'result' && (
          <Button title="メニューに戻る" onPress={() => goMode('menu')} />
        )}
      </SafeAreaView>
    </SafeAreaProvider>
  );
}

const styles = StyleSheet.create({
  container: { flex: 1, padding: 16 },
  menuContainer: { flexDirection: 'row', flexWrap: 'wrap', justifyContent: 'space-between', marginBottom: 24 },
  menuCard: { width: '48%', backgroundColor: '#f0f8ff', borderRadius: 8, padding: 16, alignItems: 'center', marginBottom: 12, shadowColor: '#000', shadowOpacity: 0.1, shadowOffset: { width: 0, height: 2 }, shadowRadius: 4 },
  menuIcon: { fontSize: 32, marginBottom: 8 },
  menuText: { fontSize: 16, fontWeight: '600' },
  gamificationHeader: { marginBottom: 16 },
  xpText: { fontSize: 18, fontWeight: 'bold' },
  xpBar: { fontSize: 16, marginVertical: 4 },
  dailyText: { fontSize: 16 },
  streakText: { fontSize: 16, color: '#ff6b35' },
  subtitle: { fontSize: 20, marginBottom: 12 },
  sentence: { fontSize: 22, marginBottom: 8 },
  prompt: { fontSize: 16, marginBottom: 8 },
  input: { borderWidth: 1, borderColor: '#888', padding: 8, fontSize: 18, marginBottom: 8 },
  feedbackContainer: { marginTop: 8 },
  feedback: { fontSize: 18, marginBottom: 8 },
  diff: { marginTop: 12, color: '#666' },
  item: { padding: 8, borderBottomWidth: 1, borderColor: '#ddd' },
  resultContainer: { flex: 1, justifyContent: 'center', alignItems: 'center' },
  resultText: { fontSize: 28, fontWeight: 'bold', marginBottom: 16 },
  xpEarnedText: { fontSize: 22, color: '#4CAF50', fontWeight: 'bold', marginBottom: 8 },
  levelInfoText: { fontSize: 18, color: '#666', marginBottom: 16  }
});

