/*
  ファイル: db.js
  既存のテーブルに加え、オフラインゲーミフィケーション用の3テーブルを追加し、
  ユーザ状態、デイリーチャレンジ、セッション記録のCRUD関数を実装。
*/
import * as SQLite from 'expo-sqlite';

// 学年ごとに分割したJSONファイルを静的インポート
import data1 from './assets/kanji/grade1.json';
import data2 from './assets/kanji/grade2.json';
import data3 from './assets/kanji/grade3.json';
import data4 from './assets/kanji/grade4.json';
import data5 from './assets/kanji/grade5.json';
import data6 from './assets/kanji/grade6.json';

// TSVファイルを静的インポート
import kanjiGrade1TSV from './assets/kanji_grade1.js';
import kanjiGrade2TSV from './assets/kanji_grade2.js';
import kanjiGrade3TSV from './assets/kanji_grade3.js';
import kanjiGrade4TSV from './assets/kanji_grade4.js';
import kanjiGrade5TSV from './assets/kanji_grade5.js';
import kanjiGrade6TSV from './assets/kanji_grade6.js';

const kanjiData = [...data1, ...data2, ...data3, ...data4, ...data5, ...data6];

export const openDb = async () => await SQLite.openDatabaseAsync('kanji.db');

export const initDatabase = async () => {
  const db = await openDb();
  
  try {
    // 既存テーブル
    await db.execAsync(`CREATE TABLE IF NOT EXISTS kanji (
      id INTEGER PRIMARY KEY AUTOINCREMENT,
      kanji TEXT NOT NULL,
      grade INTEGER NOT NULL
    );`);
    
    await db.execAsync(`CREATE TABLE IF NOT EXISTS examples (
      id INTEGER PRIMARY KEY AUTOINCREMENT,
      kanji_id INTEGER NOT NULL,
      sentence TEXT NOT NULL,
      target TEXT NOT NULL,
      reading TEXT NOT NULL,
      difficulty INTEGER NOT NULL
    );`);
    
    // Gamification テーブル追加
    await db.execAsync(`CREATE TABLE IF NOT EXISTS user_state (
      device_id TEXT PRIMARY KEY,
      xp_total INTEGER DEFAULT 0,
      current_level INTEGER DEFAULT 1,
      xp_to_next_level INTEGER DEFAULT 100,
      streak_count INTEGER DEFAULT 0,
      last_play_date TEXT
    );`);
    
    await db.execAsync(`CREATE TABLE IF NOT EXISTS daily_challenge (
      challenge_date TEXT PRIMARY KEY,
      goal INTEGER,
      progress INTEGER DEFAULT 0,
      is_completed INTEGER DEFAULT 0
    );`);
    
    await db.execAsync(`CREATE TABLE IF NOT EXISTS session_record (
      session_id TEXT PRIMARY KEY,
      date TEXT,
      correct_count INTEGER,
      total_count INTEGER,
      avg_response_ms INTEGER,
      stars INTEGER
    );`);
    
    console.log('✅ Tables created & extended for gamification');
  } catch (err) {
    console.error('DB init error:', err);
  }
};

export const insertInitialDataIfNeeded = async () => {
  try {
    const db = await SQLite.openDatabaseAsync('kanji.db');
    
    // 既存データをチェック
    const result = await db.getFirstAsync('SELECT COUNT(*) as count FROM kanji;');
    const count = result.count;
    
    console.log(`Current kanji count in DB: ${count}`);
    
    if (count === 0) {
      console.log('No data found, loading from JSONs and TSV...');
      
      // 既存のJSONデータを読み込み
      for (const k of kanjiData) {
        const kanjiResult = await db.runAsync(
          'INSERT INTO kanji (kanji, grade) VALUES (?, ?);',
          [k.kanji, k.grade]
        );
        const kanjiId = kanjiResult.lastInsertRowId;
        
        for (const ex of k.examples) {
          await db.runAsync(
            `INSERT INTO examples (kanji_id, sentence, target, reading, difficulty) VALUES (?, ?, ?, ?, ?);`,
            [kanjiId, ex.sentence, ex.target, ex.reading, ex.difficulty]
          );
        }
      }
      console.log(`✅ Loaded ${kanjiData.length} kanji from JSONs`);
    }
    
    // TSVファイルからの追加データを読み込み
    await loadAllGradesKanjiFromTSV();
    
  } catch (error) {
    console.error('Data initialization error:', error);
  }
};

// ユーザ状態取得 or 初期化
export const getUserState = async () => {
  const db = await SQLite.openDatabaseAsync('kanji.db');
  const today = new Date().toISOString().slice(0,10);
  
  try {
    const result = await db.getFirstAsync(
      'SELECT * FROM user_state WHERE device_id = ?;',
      ['DEVICE']
    );
    
    if (result) {
      return result;
    } else {
      // 初期データを挿入
      await db.runAsync(
        `INSERT INTO user_state (device_id, last_play_date) VALUES (?, ?);`,
        ['DEVICE', today]
      );
      return { 
        device_id: 'DEVICE', 
        xp_total: 0, 
        current_level: 1, 
        xp_to_next_level: 100, 
        streak_count: 0, 
        last_play_date: today 
      };
    }
  } catch (error) {
    console.error('getUserState error:', error);
    throw error;
  }
};

// ユーザ状態更新
export const updateUserState = async ({ isCorrect = false, resetStreak = false, dailyBonusXP = 0 }) => {
  try {
    const state = await getUserState();
    console.log('Current state:', state);
    
    // XP計算
    let awardedXP = 0;
    let newStreak = state.streak_count;
    
    if (isCorrect) {
      // 基本XP = 10
      awardedXP = 10;
      
      // 連続正解ボーナス計算
      newStreak = state.streak_count + 1;
      const streakBonus = Math.floor(newStreak / 5) * 5;
      awardedXP += streakBonus;
      
      console.log(`XP awarded: ${awardedXP} (base: 10, streak bonus: ${streakBonus}, daily bonus: ${dailyBonusXP})`);
    } else if (resetStreak) {
      newStreak = 0;
    }
    
    // デイリーチャレンジボーナス
    awardedXP += dailyBonusXP;
    
    // XP合計更新
    let totalXP = state.xp_total + awardedXP;
    let nextXP = state.xp_to_next_level - awardedXP;
    let newLevel = state.current_level;
    
    // レベルアップ計算
    while (nextXP <= 0) {
      newLevel += 1;
      const requiredXP = Math.floor(100 * Math.pow(newLevel, 1.5));
      nextXP += requiredXP;
      console.log(`Level up! New level: ${newLevel}, Required XP for next: ${requiredXP}`);
    }
    
    const today = new Date().toISOString().slice(0, 10);
    const db = await SQLite.openDatabaseAsync('kanji.db');
    
    await db.runAsync(
      `UPDATE user_state SET xp_total=?, current_level=?, xp_to_next_level=?, streak_count=?, last_play_date=? WHERE device_id=?;`,
      [totalXP, newLevel, nextXP, newStreak, today, 'DEVICE']
    );
    
    const result = {
      xp_total: totalXP, 
      current_level: newLevel, 
      xp_to_next_level: nextXP, 
      streak_count: newStreak,
      awarded_xp: awardedXP,
      level_up: newLevel > state.current_level
    };
    
    console.log('Updated state:', result);
    return result;
    
  } catch (error) {
    console.error('updateUserState error:', error);
    throw error;
  }
};

// デイリーチャレンジ取得 or 初期化
export const getDailyChallenge = async () => {
  const db = await SQLite.openDatabaseAsync('kanji.db');
  const today = new Date().toISOString().slice(0,10);
  
  try {
    const result = await db.getFirstAsync(
      'SELECT * FROM daily_challenge WHERE challenge_date = ?;',
      [today]
    );
    
    if (result) {
      return result;
    } else {
      const goal = Math.floor(Math.random()*5)+3; // 3~7
      await db.runAsync(
        'INSERT INTO daily_challenge (challenge_date, goal) VALUES (?,?);',
        [today, goal]
      );
      return { challenge_date: today, goal, progress: 0, is_completed: 0 };
    }
  } catch (error) {
    console.error('getDailyChallenge error:', error);
    throw error;
  }
};

// チャレンジ進捗更新
export const incrementDailyChallenge = async () => {
  try {
    const ch = await getDailyChallenge();
    if (ch.is_completed) return { ...ch, bonus_xp: 0 };
    
    const prog = ch.progress + 1;
    const wasCompleted = ch.is_completed === 1;
    const isNowCompleted = prog >= ch.goal;
    const bonusXP = (!wasCompleted && isNowCompleted) ? 50 : 0; // デイリーチャレンジ完了ボーナス
    
    const db = await SQLite.openDatabaseAsync('kanji.db');
    await db.runAsync(
      `UPDATE daily_challenge SET progress=?, is_completed=? WHERE challenge_date=?;`,
      [prog, isNowCompleted ? 1 : 0, ch.challenge_date]
    );
    
    console.log(`Daily challenge updated: ${prog}/${ch.goal}, bonus XP: ${bonusXP}`);
    return { 
      ...ch, 
      progress: prog, 
      is_completed: isNowCompleted ? 1 : 0,
      bonus_xp: bonusXP
    };
  } catch (error) {
    console.error('incrementDailyChallenge error:', error);
    throw error;
  }
};

// セッション記録挿入
export const insertSessionRecord = ({ sessionId, date, correctCount, totalCount, avgMs, stars }) => {
  return new Promise((resolve, reject) => {
    const db = openDb();
    db.transaction(tx => {
      tx.executeSql(
        `INSERT INTO session_record (session_id,date,correct_count,total_count,avg_response_ms,stars)
         VALUES (?,?,?,?,?,?);`,
        [sessionId,date,correctCount,totalCount,avgMs,stars],
        () => {
          console.log('Session record inserted successfully');
          resolve();
        },
        (_, error) => {
          console.error('Session insert error:', error);
          reject(error);
        }
      );
    }, error => {
      console.error('Transaction error:', error);
      reject(error);
    });
  });
};

// CSV/TSVデータを読み込んでDBに挿入する関数
export const importCSVData = async (csvText) => {
  try {
    const db = await SQLite.openDatabaseAsync('kanji.db');
    
    // CSVを行ごとに分割
    const lines = csvText.trim().split('\n');
    const headers = lines[0].split('\t'); // TSVの場合は\t、CSVの場合は,
    
    console.log('CSV Headers:', headers);
    console.log('Total lines:', lines.length);
    
    // ヘッダー行をスキップしてデータ行を処理
    for (let i = 1; i < lines.length; i++) {
      const values = lines[i].split('\t'); // TSVの場合は\t、CSVの場合は,
      
      if (values.length < 5) {
        console.warn(`Skipping line ${i}: insufficient columns`);
        continue;
      }
      
      const [kanji, grade, sentence, target, reading, difficulty] = values;
      
      // 漢字テーブルに挿入（重複チェック）
      let kanjiResult = await db.getFirstAsync(
        'SELECT id FROM kanji WHERE kanji = ?',
        [kanji]
      );
      
      let kanjiId;
      if (!kanjiResult) {
        const result = await db.runAsync(
          'INSERT INTO kanji (kanji, grade) VALUES (?, ?)',
          [kanji, parseInt(grade)]
        );
        kanjiId = result.lastInsertRowId;
      } else {
        kanjiId = kanjiResult.id;
      }
      
      // 例文テーブルに挿入
      await db.runAsync(
        'INSERT INTO examples (kanji_id, sentence, target, reading, difficulty) VALUES (?, ?, ?, ?, ?)',
        [kanjiId, sentence, target, reading, parseInt(difficulty)]
      );
      
      if (i % 100 === 0) {
        console.log(`Processed ${i}/${lines.length - 1} rows`);
      }
    }
    
    console.log(`✅ Successfully imported ${lines.length - 1} rows`);
    return { success: true, count: lines.length - 1 };
    
  } catch (error) {
    console.error('CSV import error:', error);
    return { success: false, error };
  }
};

// CSV/TSVをJSONに変換する関数
export const convertCSVToJSON = (csvText) => {
  const lines = csvText.trim().split('\n');
  const headers = lines[0].split('\t'); // TSVの場合は\t、CSVの場合は,
  
  console.log('Converting CSV to JSON, headers:', headers);
  
  const jsonData = [];
  
  for (let i = 1; i < lines.length; i++) {
    const values = lines[i].split('\t'); // TSVの場合は\t、CSVの場合は,
    
    if (values.length < 5) continue;
    
    const [kanji, grade, sentence, target, reading, difficulty] = values;
    
    // 既存の漢字を探す
    let existingKanji = jsonData.find(item => item.kanji === kanji);
    
    if (!existingKanji) {
      existingKanji = {
        kanji: kanji,
        grade: parseInt(grade),
        examples: []
      };
      jsonData.push(existingKanji);
    }
    
    // 例文を追加
    existingKanji.examples.push({
      sentence: sentence,
      target: target,
      reading: reading,
      difficulty: parseInt(difficulty)
    });
  }
  
  console.log(`✅ Converted to JSON: ${jsonData.length} unique kanji`);
  return jsonData;
};

